.hero-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../img/hero.jpg");
  min-height: 70vh;
  margin: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 700px;
}

.hero-text {
  text-align: center;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  color: white;
}

.hero-title {
  font-weight: bold;
  font-size: 128px;
}

.hero-subtitle {
  font-style: italic;
  font-size: 24px;
}

.gradient {
  position: absolute;
  bottom: 0;
  height: 20%;
  width: 100%;
  background-image: linear-gradient(transparent, black 40%);
}
