.production {
  max-width: 250px;
  margin: 0 auto;
}

.production-cover {
  max-width: 200px;
  aspect-ratio: 2/3;
  border-radius: 0.2rem;
  transition: 200ms;
}

.production-cover img {
  width: 100%;
  height: 100%;
  vertical-align: center;
}

.production-cover:hover {
  transform: scale(1.01);
  transition: 200ms;
  cursor: pointer;
}

.production-info {
  margin: 0;
  margin-top: 10px;
  font-size: 12px;
  font-weight: normal;
}

.production-title {
  margin: 0;
  font-size: 24px;
  font-weight: bolder;
}
